.range {
  width: 50%;
  margin: 13.8px 0;
  background-color: transparent;
  -webkit-appearance: none;
}
.range:focus {
  outline: none;
}
.range::-webkit-slider-runnable-track {
  background: #3071a9;
  border: 0.2px solid #010101;
  border-radius: 1.3px;
  width: 100%;
  height: 8.4px;
  cursor: pointer;
}
.range::-webkit-slider-thumb {
  margin-top: -14px;
  width: 16px;
  height: 36px;
  background: #e7ffff;
  border: 1px solid #000000;
  border-radius: 3px;
  cursor: pointer;
  -webkit-appearance: none;
}
.range:focus::-webkit-slider-runnable-track {
  background: #367ebd;
}
.range::-moz-range-track {
  background: #3071a9;
  border: 0.2px solid #010101;
  border-radius: 1.3px;
  width: 100%;
  height: 8.4px;
  cursor: pointer;
}
.range::-moz-range-thumb {
  width: 16px;
  height: 36px;
  background: #e7ffff;
  border: 1px solid #000000;
  border-radius: 3px;
  cursor: pointer;
}
.range::-ms-track {
  background: transparent;
  border-color: transparent;
  border-width: 14.8px 0;
  color: transparent;
  width: 100%;
  height: 8.4px;
  cursor: pointer;
}
.range::-ms-fill-lower {
  background: #2a6495;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
}
.range::-ms-fill-upper {
  background: #3071a9;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
}
.range::-ms-thumb {
  width: 16px;
  height: 36px;
  background: #e7ffff;
  border: 1px solid #000000;
  border-radius: 3px;
  cursor: pointer;
  margin-top: 0px;
  /*Needed to keep the Edge thumb centred*/
}
.range:focus::-ms-fill-lower {
  background: #3071a9;
}
.range:focus::-ms-fill-upper {
  background: #367ebd;
}
